#main-container{
    height: 100%;
    display: flex;
    justify-content:  center;
    align-items: center;
}

#main-container form {
    width: 300px;

    display: flex;
    flex-direction: column;
}

#main-container form input{
    height: 40px;
    border: 1px solid #DDD;
    border-radius: 4px;
    font-size: 16px;
    padding: 0 20px;
    margin-top: 30px;
}

#main-container form button{
    height: 40px;
    background: #7159c1;
    border-radius: 4px;
    font-size: 16px;
    padding: 0 20px;
    margin-top: 30px;
    color: #FFF;
    font-weight: bold;
    border: 0;
    cursor: pointer;

}

#main-container form button:hover{
    opacity: 0.8;
}